.chatflow-save-dialog {
  width: 450px;

  h2[mat-dialog-title] {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 0;
    padding: 20px 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    mat-icon {
      font-size: 28px;
      width: 28px;
      height: 28px;
    }

    span {
      font-size: 20px;
      font-weight: 500;
    }
  }

  mat-dialog-content {
    padding: 24px;
  }

  mat-dialog-actions {
    padding: 16px 24px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    margin: 0;
    gap: 12px;

    button {
      display: flex;
      align-items: center;
      gap: 6px;

      mat-spinner {
        margin-right: 4px;
      }
    }
  }
}

.description {
  margin: 0 0 20px 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  line-height: 1.5;
}

.name-field {
  width: 100%;
  margin-bottom: 20px;

  ::ng-deep .mat-mdc-form-field-flex {
    background: rgba(255, 255, 255, 0.05);
  }
}

.save-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.info-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: rgba(100, 181, 246, 0.8);
  }
}

.update-notice {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 16px;
  padding: 12px 16px;
  background: rgba(255, 152, 0, 0.1);
  border-radius: 6px;
  border: 1px solid rgba(255, 152, 0, 0.3);

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: #FF9800;
  }

  span {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.9);
  }
}
